.TBpopularPosts {
    width: 380px;
    height: 580px;
    margin: 10px auto 20px 0;
    border: 2px solid rgb(235, 235, 235);
    box-shadow: rgba(104, 104, 104, 0.2) 0px 2px 8px 0px;
    background-color: #FCFCFC;
}
.TBpopularHeader { 
    text-align: left;
    margin: 20px 0 20px 15px;
    line-height: 3rem;
    font-size: 1.5rem;
}
.TBpopularList { 
    margin: 0;
    margin-top: 10px;
    padding: 0;
}
.TBpopularList li {
    list-style: none;
    margin: 0;
}

@media(max-width: 800px) { 
    .TBpopularPosts { 
        width: 90%;
        margin: 10px auto;
    }
    .TBpopularHeader { 
        width: 90%;
    }
    .TBpopularList { 
        width: 100%;
    }
    .TBpopularList li { 
        width: 90%;
        max-width: 90%;
        /* border: 1px solid red; */
    }
}