.TBTravelBlog {
    margin: 0;
    padding: 0;
    background-color:rgb(227, 213, 200);
    padding-top: 10px;
    box-sizing: border-box;
    width: 100vw;
    max-width: 100vw;
    position:absolute;
    /* min-height: 440vh; */
    height: fit-content;
}
.TBprojectBoxMargin { 
    height: fit-content;
    /* min-height: 390vh; */
    width: 90%;
    max-width: 90vw;
    margin:  0 auto 200px;
    border: 1px solid #ccc;
    overflow: scroll;
    box-shadow: -11px 9px 18px -1px rgba(105, 105, 105, 0.3);
    -webkit-box-shadow: -11px 9px 18px -1px rgba(105, 105, 105, 0.3);
    -moz-box-shadow: -11px 9px 18px -1px rgba(105, 105, 105, 0.3);
    background-color: #FCFCFC;
}
.projectBoxTitle { 
    margin-top: 150px;
    margin-bottom: 40px;
    width: 200px;
}

@media (max-width: 600px) { 
    .TravelBlog { 
        height: 200vh;
    }
